
import { motion } from "framer-motion";
import homeDistance from "../assets/home-distance.png";
import homeLink from "../assets/home-link.png";
import Map from '../components/Map';

//var jsonData = require("../data/pages.json");

export default function Home() {


  // const mapItems = (x) => {
    
  //     return (
  //       <motion.div key={x.id} variants={item}>
  //         <motion.div
  //           whileHover={{ scale: 1.1 }}
  //           whileTap={{ scale: 0.8 }}
  //           className="item"
  //           onClick={() => {
  //             handleClick(x.id);
  //           }}
  //         >
  //           {x.name}
  //         </motion.div>
  //       </motion.div>
  //     );
    
  // };

  return (
    <div className="home-page">
      <motion.div
        className="home-page-bg grayscale"
        initial={{ opacity: 0 }}
        animate={{ opacity: 1 }}
        transition={{ duration: 0.5 }}
      ></motion.div>
      
       <Map/>
     
      <motion.div
        className="home-distance"
        initial={{ opacity: 0, scale: 0.5, y: 300 }}
        animate={{ opacity: 1, scale: 1, y: 0 }}
        transition={{ duration: 0.5,delay: 0.3 }}
      >
        <a
          href="https://goo.gl/maps/3PMVW1aPY1hUmp4x7"
          target="_blank"
          rel="noreferrer"
        >
          <img src={homeDistance} alt="" />
        </a>
      </motion.div>
      <motion.div
        className="home-link"
        initial={{ opacity: 0, scale: 0.5, y: 300 }}
        animate={{ opacity: 1, scale: 1, y: 0 }}
        transition={{ duration: 0.5, delay: 0.5 }}
      >
        <a href="https://zanjan.mcth.ir/" target="_blank" rel="noreferrer">
          <img src={homeLink} alt="" />
        </a>
      </motion.div>
    </div>
  );
}
